.App-header {
  background-image: url(./treasurehunt.jpg);
  background-size: cover;
  background-position: top;

  margin: auto;
  /* min-height: 100%; */
  height: 100vh;
  text-align: center;
  width: 80%;
  /* position: relative; */
  background-repeat: "no-repeat";
}
.headline {
  background-color: antiquewhite;
  width: 50%;
  padding: 5px;
  height: auto;
  text-align: center;

  margin-left: 45%;
  margin-right: 50%;
}
.help {
  width: 10%;
  padding: 5px;
  height: auto;
  text-align: center;
  margin-top: 0;
  margin-left: 95%;
  margin-right: 50%;
}
/* Create two equal columns that floats next to each other */
.column1 {
  float: left;
  width: 75%;
  padding: 10px;
}
.column2 {
  margin-top: 2%;
  float: right;
  width: 20%;
  padding: 10px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}
h1 {
  font-weight: bold;
  font-family: "BradleyHandITCBold";
  font-size: 42px;
}
.pinpoint {
  color: blueviolet;
}
.location1 {
  margin-top: 45%;
  margin-right: 73%;
}
.location2 {
  margin-top: -15%;
  margin-right: 60%;
}
.location3 {
  margin-top: 2%;
  margin-right: 20%;
}
.location4 {
  margin-top: -11%;
  margin-left: 42%;
}
.location5 {
  margin-top: -18%;
}
.location6 {
  margin-top: -25%;
  margin-left: 10%;
}
.level-button {
  background-color: #04aa6d;
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  border-radius: 8px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.help-button {
  background-color: #3c6c59;
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  border-radius: 8px;
  width: 220px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
button[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

.dialogButton {
  background-color: #04aa6d;
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  border-radius: 8px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 20%;
}

.closeButton {
  background-color: #aa0404;
  border: none;
  color: white;
  padding: 10px;
  text-align: center;
  border-radius: 8px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 20%;
}

@media screen and (max-width: 1500px) {
  .location1 {
    margin-top: 45%;
    margin-right: 73%;
  }
  .location2 {
    margin-top: -20%;
    margin-right: 60%;
  }
  .location3 {
    margin-top: -2%;
    margin-right: 20%;
  }
  .location4 {
    margin-top: -11%;
    margin-left: 52%;
  }
  .location5 {
    margin-top: -25%;
  }
  .location6 {
    margin-top: -25%;
    margin-left: 10%;
  }
  .headline {
    background-color: antiquewhite;
    width: 50%;
    padding: 5px;
    height: auto;
    text-align: center;
    font-size: 10px;
    margin-left: 45%;
    margin-right: 50%;
  }
  h1 {
    font-weight: bold;
    font-size: 30px;
  }
}
